.nav__section{
    background: #F5F5F5;
}
.nav {
    margin: auto;
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    width: 1205px;
    height: 42px;
    background: #F5F5F5;
    align-items: center;
}
.nav__title{
    font-size: 14px;
    color: #666666;
    font-weight: 400;
}
.nav__list{
    display: flex;
    width: 410px;
    align-items: center;
    justify-content: space-between;
    list-style: none;
}
.list__item{
    display: flex;
    align-items: center;
}
.list__item a {
    margin-left: 6px;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #666666;
    text-decoration: none;
}
.list__item a span {
    font-weight: bold;
}
.list__item a:hover {
    text-decoration:underline;
}
.hr{
    width: 18px;
    height: 1px;
    transform: rotate(90deg);
    background: #D9D9D9;
}